













import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class GetHeight extends Vue {
  @Prop() private msg?: string;
  private height: string = '';
  private showNull: boolean = false;

  private submit() {
    if (this.height && this.height.trim()) {
      this.showNull = false;
      alert("Bạn cao: " + this.height.trim() + " cm");
    }
    else {
      this.showNull = true;
    }
  }
}
